.Capa{
    width: 100%;
    height:100vh;
    padding: 0px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
video{
    width: inherit;
    height: inherit;
    object-fit: cover;
    z-index: -1;    
}
.Capa--info{
    z-index: 5; 
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Capa--info h3{
    width: 100%;
    z-index: 5;
    color: #fff;
    font-weight: bold;
    margin-top: -200px
}
.Capa--info .Capa--seta--down{
    animation: MoveUpDown .5s linear  1s infinite alternate;
    font-size:80px;
    filter:invert(1);
    margin-top: 0px
}
@keyframes MoveUpDown {
    from {
        font-size: 50px;
    }
    to { 
        font-size: 60px;
    }
}