.Capa{
    width: 100%;
    height:100vh;
    padding: 0px;
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
video{
    width: inherit;
    height: inherit;
    object-fit: cover;
    z-index: -1;    
}
.Capa--info{
    z-index: 5; 
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Capa--info h3{
    width: 100%;
    z-index: 5;
    color: #fff;
    font-weight: bold;
    margin-top: -200px
}
.Capa--info .Capa--seta--down{
    -webkit-animation: MoveUpDown .5s linear  1s infinite alternate;
            animation: MoveUpDown .5s linear  1s infinite alternate;
    font-size:80px;
    -webkit-filter:invert(1);
            filter:invert(1);
    margin-top: 0px
}
@-webkit-keyframes MoveUpDown {
    from {
        font-size: 50px;
    }
    to { 
        font-size: 60px;
    }
}
@keyframes MoveUpDown {
    from {
        font-size: 50px;
    }
    to { 
        font-size: 60px;
    }
}
.Pag02{
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;  
}
.Pag02 h1{ 
 margin-top: 50px;
}
.Pag02 .Card--container{   
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;   
}
.Pag02 .Card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;  
    margin: 2px auto ;
    cursor: pointer; 
    width: 200px; 
    height: 200px; 
    margin-top: 30px;   
}
.Pag02 .Card h3{
    font-weight: bold;
    font-size:smaller;
}
.Pag02 .Card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 100%;
    -webkit-transform: scale(.9);
            transform: scale(.9);
    transition: .5s;
    border-radius: 2px solid ;
}
.Pag02 .Card:hover img{
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: .5s;
}
