.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 9;
}
.Header--logo{
    height: 40px;
    margin: 5px;
    cursor: pointer;
    display:inline-flex;
    justify-content: center;
    align-items: center;
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--logo h4{
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-left: 5px;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center; 
}
.Header--menu button{
    background-color: #14141400;
    margin: 0px 5px;
    padding: 0px 5px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.Header--contact{
    height: 25px;
    margin: 0px 5px;
    display: inline-flex;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;    
}
.Header--contact img{
    width: auto;
    height: 100%;
}
@media (max-width: 600px){ 
    .Header--logo h4{
        opacity: 0;
    }
}
.Footer{
    height: 100%;
    width: 100%;
    margin-top: -4px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}
.Protifolio{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card--conteriner{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Protifolio .card
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 49%;
    border-radius: 2px;
    padding: 5px;
}
.Protifolio .card iframe,
.Protifolio .card img
{    
    height: 60vh;
    width: calc(100% - 4px);
    padding:1px;
    object-fit: cover;
    object-position: center;
}
.Protifolio .card--seta--left, .Protifolio .card--seta--right{
    position:absolute;
    width:40px;
    background-color: rgba(255,255, 255, .6);
    -webkit-filter:invert(1);
            filter:invert(1);
    border-radius: 100%;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;    
    padding: 0px 5px;
    overflow:hidden;
    opacity: 1;
    transition: 0.5s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
}
.Protifolio .card--seta--left{
    left:51%;
}
.Protifolio .card--seta--right{
    right:15px;
}

@media (max-width: 700px) {
    .Protifolio .card iframe, .Protifolio .card img, .Protifolio .card iframe {height: 60vh;}   
    .Protifolio .card {width: 98%;}    
    .Protifolio .card--seta--left{left:10px;}
    .Protifolio .card--seta--right{right:10px;}   
}
