.Pag02{
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;  
}
.Pag02 h1{ 
 margin-top: 50px;
}
.Pag02 .Card--container{   
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;   
}
.Pag02 .Card{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;  
    margin: 2px auto ;
    cursor: pointer; 
    width: 200px; 
    height: 200px; 
    margin-top: 30px;   
}
.Pag02 .Card h3{
    font-weight: bold;
    font-size:smaller;
}
.Pag02 .Card img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 100%;
    transform: scale(.9);
    transition: .5s;
    border-radius: 2px solid ;
}
.Pag02 .Card:hover img{
    transform: scale(1);
    transition: .5s;
}