.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: nowrap;
    z-index: 9;
}
.Header--logo{
    height: 40px;
    margin: 5px;
    cursor: pointer;
    display:inline-flex;
    justify-content: center;
    align-items: center;
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--logo h4{
    color: rgb(255, 255, 255);
    cursor: pointer;
    margin-left: 5px;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center; 
}
.Header--menu button{
    background-color: #14141400;
    margin: 0px 5px;
    padding: 0px 5px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer;
}
.Header--contact{
    height: 25px;
    margin: 0px 5px;
    display: inline-flex;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;    
}
.Header--contact img{
    width: auto;
    height: 100%;
}
@media (max-width: 600px){ 
    .Header--logo h4{
        opacity: 0;
    }
}