.ComoFunciona{
    width: 100%; 
    min-height: 60vh ;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: nowrap;
}
.ComoFunciona h1{ 
    margin-top: 50px;
}
.ComoFunciona .Card--container{   
    margin-top: 30px; 
    width: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;   
}
.ComoFunciona .Card{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;  
    margin: 5px auto ;
    cursor: pointer;
    width: 350px;
}
.ComoFunciona .Card h3{
    font-size:14pt;
}
.ComoFunciona .Card h4{
    font-size:smaller;
}
.ComoFunciona .Card img{
    width: 80px;
    height: 80px;
    object-fit: cover;
    border-radius: 2px;
    transform: scale(.9);
    transition: .5s;
    border-radius: 2px solid;
}
.ComoFunciona .Card:hover img{
    transform: scale(1);
    transition: .5s;
}

